import { LOGIN_SUCCESS, LOGIN_FAILURE, LOGOUT } from "../actions/types";

let initialState = {
  logined: true,
  userData: {},
};

export default function authReducer(state = initialState, action) {
  switch (action.type) {
    case LOGIN_SUCCESS:
      return {
        ...state,
        logined: true, 
        userData: action.payload
      }

      case LOGOUT:
      return {
        ...state,
        logined: false, 
      }

      case LOGIN_FAILURE:
      return {
        ...state,
        logined: false, 
      }
      
    default:
      return state
  }
}
