import { NavLink, useParams } from "react-router-dom";
import { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";
import { Link } from "react-router-dom";
import AdminComponent from "../components/admin/AdminComponents";
import { getMeAction } from "../store/actions/auth";

export default function Admin() {
  let { path } = useParams();
  let dispatch = useDispatch();

  let { logined } = useSelector((state) => state.auth);

  useEffect(() => {
    let token = localStorage.getItem("x_token");
    dispatch(getMeAction(token));
  }, []);

  return (
    <>
      {logined ? (
        <div className="container-fluid">
          <div className="row">
            <div className="col-3 sidebar">
              <NavLink
                to="/admin/attendance"
                className="btn btn-nav"
                activeClassName="active"
              >
                Ведомость посещаемости
              </NavLink>

              <NavLink
                to="/admin/report"
                className="btn btn-nav"
                activeClassName="active"
              >
                Отчет посещаемости
              </NavLink>

              <NavLink
                to="/admin/groups"
                className="btn btn-nav"
                activeClassName="active"
              >
                Группы
              </NavLink>

              <NavLink
                to="/admin/children"
                className="btn btn-nav"
                activeClassName="active"
              >
                Воспитанники
              </NavLink>
            </div>

            <div className="col-9 content">
              <AdminComponent path={path} />
            </div>
          </div>
        </div>
      ) : (
        <div className="alert alert-danger">
          You are not authorised! <Link to="/login">Login</Link>
        </div>
      )}
    </>
  );
}
