export default function ChildVisit({ data, visitChildren, setVisitChildren }) {
  const childVisited = visitChildren.includes(data.id);

  return (
    <>
      <div className="child">
        <div className="row">
          <div className="col-8"> {data.fullName} </div>
          <div className="col-2">
            {/* <label className="form-check-label" for="visitChild">
              Присутсвовал
            </label> */}
            <input
              checked={childVisited}
              onChange={(event) => {
                event.target.checked
                  ? setVisitChildren([...visitChildren, data.id])
                  : setVisitChildren(
                      visitChildren.filter((childId) => childId !== data.id)
                    );
              }}
              className="form-check-input"
              type="checkbox"
              value="false"
              name="visit"
              id="visitChild"
            />
          </div>
        </div>
      </div>
    </>
  );
}
