import { useState } from "react";

export default function Register() {
  let [regForm, setRegForm] = useState({});

  let handleChange = (e) => {
    setRegForm({
      ...regForm,
      [e.target.name]: e.target.value,
    });
  };

  let registerHandler = (e) => {
    e.preventDefault();

    let reguest = {
      method: "POST",
      body: JSON.stringify(regForm),
      headers: {
        "Content-Type": "application/json",
      },
    };

    fetch(
      "https://kinder-garden-project.herokuapp.com/api/users/sign-up-model",
      reguest
    )
      .then((r) => r.json())
      .then((data) => {
        if (data.token) {
          localStorage.setItem("x_token", data.token);
          window.location.href = "/";
        }
      });
  };

  return (
    <>
      <div className="register-form">
        <h4>Регистрация</h4>
        <form onSubmit={registerHandler}>
          <label for="username" className="form-label">
            Ваше имя
          </label>
          <input
            className="form-control"
            type="text"
            id="username"
            value={regForm.userName}
            name="username"
            onChange={handleChange}
          />
          <label for="username" className="form-label">
            Логин
          </label>
          <input
            className="form-control"
            type="text"
            id="fullname"
            value={regForm.fullName}
            name="fullName"
            onChange={handleChange}
          />
          <label for="username" className="form-label">
            Пароль
          </label>
          <input
            className="form-control"
            type="password"
            id="password"
            value={regForm.password}
            name="password"
            onChange={handleChange}
          />

          <button className="mt-3 btn btn-add" type="submit">
            Зарегистрироваться
          </button>
        </form>
      </div>
    </>
  );
}
