import { Link } from "react-router-dom";
import Logo from "../images/Logo.png";
import { useState } from "react";
import { Modal, ModalHeader, ModalBody, ModalFooter } from "reactstrap";

export default function Header() {
  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  let [loginForm, setLoginForm] = useState({});

  let handleChange = (e) => {
    setLoginForm({
      ...loginForm,
      [e.target.name]: e.target.value,
    });
  };

  let loginHandler = (e) => {
    e.preventDefault();

    let reguest = {
      method: "POST",
      body: JSON.stringify(loginForm),
      headers: {
        "Content-Type": "application/json",
      },
    };

    fetch(
      "https://kinder-garden-project.herokuapp.com/api/users/sign-in",
      reguest
    )
      .then((r) => r.json())
      .then((data) => {
        if (data.token) {
          localStorage.setItem("x_token", data.token);
          alert("You have logined successfully");
          window.location.href = "/";
        } else {
          alert("Login Error!");
        }
      });
  };

  return (
    <>
      <div className="header">
        <Link to="/">
          <img className="logo" src={Logo} />
        </Link>
        <div>
          <button onClick={toggle} className="btn btn-login">
            Вход
          </button>
        </div>
      </div>

      <Modal isOpen={modal} toggle={toggle}>
        <ModalHeader toggle={toggle}>Вход в систему</ModalHeader>
        <ModalBody>
          <form onSubmit={loginHandler}>
            <label for="username" className="form-label">
              Логин
            </label>
            <input
              className="form-control"
              type="text"
              id="username"
              value={loginForm.username}
              name="username"
              onChange={handleChange}
            />
            <label for="password" className="form-label">
              Пароль
            </label>
            <input
              className="form-control"
              type="password"
              id="password"
              value={loginForm.password}
              name="password"
              onChange={handleChange}
            />
            <div className="end">
              <Link onClick={toggle} to="/admin/attendance">
                <button className="btn btn-add mt-3" type="submit">
                  Вход
                </button>
              </Link>
              <button className="btn btn-secondary ms-3 mt-3" onClick={toggle}>
                Отмена
              </button>
            </div>
          </form>
        </ModalBody>
        <ModalFooter>
          <Link onClick={toggle} to="/register">
            Регистрация
          </Link>
        </ModalFooter>
      </Modal>
    </>
  );
}
